.switch[type="checkbox"] {
    outline: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    position: absolute;
    opacity: 0
}

.switch[type="checkbox"]+label {
    display: inline-flex;
    position: relative;
    line-height: 36px;
    padding-left: 78px;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    cursor: pointer
}

.switch[type="checkbox"]+label::before,
.switch[type="checkbox"]+label::after {
    content: '';
    position: absolute;
    display: block
}

.switch[type="checkbox"]+label::before {
    top: 0;
    left: 0;
    width: 64px;
    height: 36px;
    border-radius: 18px;
    background: #E9EDF3
}

.switch[type="checkbox"]+label::after {
    top: 6px;
    left: 6px;
    width: 24px;
    height: 24px;
    border-radius: 12px;
    background: #fff;
    transition: all .15s ease-out
}

.switch[type="checkbox"]:checked+label::before {
    background: #535FD7
}

.switch[type="checkbox"]:checked+label::after {
    left: 34px
}

@media (min-width: 641px) {
    .field-grouped {
